import { useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Share2, Globe } from "lucide-react";
import { Button } from "@/components/ui/button";
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from "@/components/ui/dropdown-menu";
import { toast } from "sonner";
import { useSiteSettings } from "@/hooks/useSiteSettings";
import logoFallback from "@/assets/xiaoxiong-logo-v3.png";
import googlePlayBadge from "@/assets/google-play-badge-new.png";
import appStoreBadge from "@/assets/app-store-badge.svg";

const languages = [
  { code: "zh", name: "‰∏≠Êñá", flag: "üá®üá≥" },
  { code: "pt", name: "Portugu√™s", flag: "üáµüáπ" },
  { code: "en", name: "English", flag: "üá¨üáß" },
];

export default function Contact() {
  const { t, i18n } = useTranslation();
  const { settings: siteSettings } = useSiteSettings();
  const logoUrl = siteSettings?.logo_url || logoFallback;
  const currentLanguage = languages.find(lang => lang.code === i18n.language) || languages[0];

  // Set Portuguese as default language for this page
  useEffect(() => {
    const savedLang = localStorage.getItem("i18nextLng");
    if (!savedLang) {
      i18n.changeLanguage("pt");
    }
  }, [i18n]);

  const changeLanguage = (langCode: string) => {
    i18n.changeLanguage(langCode);
    localStorage.setItem("i18nextLng", langCode);
  };

  const handleShare = async () => {
    const shareData = {
      title: t("contact.title", "Contact Us"),
      text: "Xiaoxiong Market",
      url: window.location.href,
    };

    try {
      if (navigator.share && typeof navigator.canShare === 'function' && navigator.canShare(shareData)) {
        await navigator.share(shareData);
      } else {
        await navigator.clipboard.writeText(window.location.href);
        toast.success(t("common.linkCopied", "ÈìæÊé•Â∑≤Â§çÂà∂"));
      }
    } catch (error) {
      if ((error as Error).name !== "AbortError") {
        await navigator.clipboard.writeText(window.location.href);
        toast.success(t("common.linkCopied", "ÈìæÊé•Â∑≤Â§çÂà∂"));
      }
    }
  };

  return (
    <div className="min-h-screen bg-primary flex items-center justify-center relative">
      {/* Top Right Buttons */}
      <div className="absolute top-4 right-4 flex items-center gap-2">
        {/* Language Switcher */}
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <Button variant="secondary" size="icon" className="shadow-lg">
              <Globe className="w-5 h-5" />
            </Button>
          </DropdownMenuTrigger>
          <DropdownMenuContent align="end" className="bg-background">
            {languages.map(lang => (
              <DropdownMenuItem 
                key={lang.code} 
                onClick={() => changeLanguage(lang.code)} 
                className={`cursor-pointer ${currentLanguage.code === lang.code ? "bg-accent" : ""}`}
              >
                <span className="mr-2">{lang.flag}</span>
                <span>{lang.name}</span>
              </DropdownMenuItem>
            ))}
          </DropdownMenuContent>
        </DropdownMenu>

        {/* Share Button */}
        <Button
          variant="secondary"
          size="icon"
          className="shadow-lg"
          onClick={handleShare}
        >
          <Share2 className="w-5 h-5" />
        </Button>
      </div>

      <div className="container mx-auto px-4 py-16 text-center">
        <img 
          src={logoUrl} 
          alt="Xiaoxiong Market" 
          className="h-[7.5rem] sm:h-36 md:h-[10.5rem] object-contain mx-auto mb-4 drop-shadow-lg"
        />
        <h1 className="text-3xl sm:text-4xl md:text-5xl font-bold text-primary-foreground mb-4">
          Xiaoxiong Market
        </h1>
        <p className="text-primary-foreground/80 max-w-2xl mx-auto leading-relaxed mb-8">
          {t("contact.description")}
        </p>
        
        {/* App Store Badges */}
        <div className="flex flex-col sm:flex-row items-center justify-center gap-4">
          {/* App Store */}
          <a 
            href="https://apps.apple.com/cn/app/xiaoxiong/id6755481369" 
            target="_blank" 
            rel="noopener noreferrer"
            className="relative group w-[80vw] sm:w-auto"
          >
            <div className="absolute -inset-2 bg-gradient-to-r from-primary via-primary-glow to-primary rounded-2xl blur-lg opacity-40 group-hover:opacity-70 transition-opacity" />
            <div className="relative bg-black hover:bg-gray-900 h-[52px] sm:h-[56px] w-full sm:w-[180px] rounded-xl transition-all hover:scale-105 shadow-2xl hover:shadow-primary/30 border border-white/10 flex items-center justify-center">
              <img 
                src={appStoreBadge} 
                alt="Download on the App Store" 
                className="h-8 sm:h-9"
              />
            </div>
          </a>
          
          {/* Google Play */}
          <a 
            href="https://play.google.com/store/apps/details?id=com.xiaoxiong.market" 
            target="_blank" 
            rel="noopener noreferrer"
            className="relative group w-[80vw] sm:w-auto"
          >
            <div className="absolute -inset-2 bg-gradient-to-r from-primary via-primary-glow to-primary rounded-2xl blur-lg opacity-40 group-hover:opacity-70 transition-opacity" />
            <div className="relative bg-black hover:bg-gray-900 h-[52px] sm:h-[56px] w-full sm:w-[180px] rounded-xl transition-all hover:scale-105 shadow-2xl hover:shadow-primary/30 border border-white/10 flex items-center justify-center">
              <img 
                src={googlePlayBadge} 
                alt="Get it on Google Play" 
                className="h-12 sm:h-14"
              />
            </div>
          </a>
        </div>
        
        {/* Navigate to Website Button */}
        <a 
          href="https://xiaoxiongmarket.com" 
          target="_blank" 
          rel="noopener noreferrer"
          className="mt-6 inline-block w-[80vw] sm:w-auto"
        >
          <Button 
            variant="outline" 
            className="w-full sm:w-[368px] h-[52px] sm:h-[56px] text-base font-medium shadow-lg hover:scale-105 transition-all border-white text-white bg-primary hover:bg-white hover:text-primary hover:border-white"
          >
            {t("contact.visitWebsite")}
          </Button>
        </a>
      </div>
    </div>
  );
}
